function App() {
  const [forces, setForces] = useState([]);
  //const [moments, setMoments] = useState([]);
  const [furthest, setFurthest] = useState(0);
  const [moment, setMoment] = useState(0);
  const [resultante, setResultante] = useState(
    new ForceData({ id: "Force Résultante" })
  );

  const UpdateForce = () => {
    setForces([...forces]);
    let best = 0;
    forces.forEach((item) => {
      if (Math.abs(parseFloat(item.posX)) > best)
        best = Math.abs(parseFloat(item.posX));
      if (Math.abs(parseFloat(item.posY)) > best)
        best = Math.abs(parseFloat(item.posY));
    });
    best = Math.abs(best);
    if (best === 0) setFurthest(canvasSize.x + canvasSize.x / 3.0);
    else setFurthest(best * 2 + best / 2.0);

    let tmpMoment = 0;
    let tmpResultante = new ForceData({ id: "Force Résultante" });
    forces.forEach((item) => {
      tmpMoment += item.moment;
      tmpResultante.fx += item.fx;
      tmpResultante.fy += item.fy;
    });
    tmpResultante.angle = (
      (Math.atan2(tmpResultante.fy, tmpResultante.fx) * 180) /
      Math.PI
    ).toPrecision(4);
    tmpResultante.module = Math.sqrt(
      (
        tmpResultante.fx * tmpResultante.fx +
        tmpResultante.fy * tmpResultante.fy
      ).toPrecision(4)
    );
    tmpResultante.info = {
      x0: { x: 0, y: tmpMoment / -tmpResultante.fx },
      y0: { x: tmpMoment / tmpResultante.fy, y: 0 },
    };
    tmpResultante.distance = tmpMoment / parseFloat(tmpResultante.module);
    //tmpResultante.posX =
    setResultante(tmpResultante);
    setMoment(tmpMoment.toPrecision(4));
  };

  const AddForce = () => {
    setForces([...forces, new ForceData({ id: forces.length })]);
  };

  const RemoveForce = (itemToRemove) => {
    setForces([...forces].filter((item) => item !== itemToRemove));
  };

  const CloneForce = (itemToClone) => {
    setForces([...forces, new ForceData({ force: itemToClone })]);
  };

  /*
  const UpdateMoment = () => {
    setMoments([...moments]);
  };

  const AddMoment = () => {
    setMoments([...moments, { value: 0 }]);
  };

  const RemoveMoment = (itemToRemove) => {
    setMoments([...moments].filter((item) => item !== itemToRemove));
  };*/

  const canvasSize = { x: 750, y: 750 };
  const setup = (p5, parentRef) => {
    p5.createCanvas(canvasSize.x, canvasSize.y).parent(parentRef);
  };
  const draw = (p5) => {
    function Rebase(pos) {
      return (pos * parseFloat(canvasSize.x)) / parseFloat(furthest);
    }

    function drawArrow(base, vec, id, size = 1, color = 0) {
      let normalized = p5.createVector(vec.x, vec.y).normalize().mult(50);
      p5.push();
      p5.stroke(color);
      p5.fill(color);
      p5.strokeWeight(3 * size);
      p5.fill(0);
      p5.translate(Rebase(parseFloat(base.x)), Rebase(parseFloat(base.y)));
      p5.line(0, 0, normalized.x, normalized.y);
      p5.rotate(normalized.heading());
      let arrowSize = 5 * size;
      p5.translate(normalized.mag() - arrowSize, 0);
      p5.triangle(0, arrowSize / 2, 0, -arrowSize / 2, arrowSize, 0);
      p5.pop();
      p5.textSize(26);
      p5.text(
        `${id}`,
        Rebase(base.x) - normalized.x / 1.25,
        Rebase(base.y) - normalized.y / 1.25
      );
      /*
      p5.text(
        `${id}`, //`${id} = [${vec.x.toPrecision(4)}, ${vec.y.toPrecision(4)}]`,
        Rebase(base.x) +
          Math.abs(normalized.x / 2.0) * 1.5 +
          normalized.x / 2.0,
        Rebase(base.y) - Math.abs(normalized.y / 2.0) * 1.5 + normalized.y / 2.0
      );*/
    }

    p5.strokeWeight(2);
    p5.background(255);
    p5.line(0, 0, canvasSize.x, 0);
    p5.line(0, 0, 0, canvasSize.y);
    p5.line(0, canvasSize.y, canvasSize.x, canvasSize.y);
    p5.line(canvasSize.x, 0, canvasSize.x, canvasSize.y);
    p5.strokeWeight(1);
    p5.line(canvasSize.x / 2, 0, canvasSize.x / 2, canvasSize.y);
    p5.line(0, canvasSize.y / 2, canvasSize.x, canvasSize.y / 2);
    p5.strokeWeight(15);
    p5.point(canvasSize.x / 2, canvasSize.y / 2);

    forces.forEach((item) => {
      if (item.module !== 0)
        drawArrow(
          p5.createVector(
            parseFloat(item.posX) + furthest / 2.0,
            -parseFloat(item.posY) + furthest / 2.0
          ),
          p5.createVector(parseFloat(item.fx), -parseFloat(item.fy)),
          item.id,
          1.5
        );
    });
  };

  return (
    <div className="App">
      <h1>Calculateur de moment de force en 2D</h1>
      <h2>
        Le moment de force de ce système est de{" "}
        {parseFloat(moment).toPrecision(4)} N•unité
      </h2>
      <h2>
        R = {resultante.fx.toPrecision(4)}i + {resultante.fy.toPrecision(4)}j
      </h2>
      <div className="container my-3">
        <div className="row mx-auto">
          <Force
            className="col-2"
            key="Force R"
            force={resultante}
            update={UpdateForce}
            remove={RemoveForce}
            clone={CloneForce}
            readOnly={true}
          />
          <Sketch
            className="ml-auto"
            style={{
              minWidth: canvasSize.x,
              maxWidth: canvasSize.x,
            }}
            setup={setup}
            draw={draw}
          />
        </div>
      </div>
      <Button onClick={AddForce}>Ajouter Force</Button>
      <CardDeck
        className="row flex-nowrap col-10 mx-auto"
        style={{ margin: "1.5rem", overflow: "auto" }}
      >
        {forces.map((force, i) => {
          return (
            <Force
              style={{ margin: "5rem" }}
              key={i}
              force={forces[i]}
              update={UpdateForce}
              remove={RemoveForce}
              clone={CloneForce}
            />
          );
        })}
      </CardDeck>
      {/*}
      <Button onClick={AddMoment}>Ajouter Moment</Button>
      <CardDeck
        className="row flex-nowrap col-10 mx-auto"
        style={{ margin: "1.5rem", overflow: "auto" }}
      >
        {moments.map((moment, i) => {
          return (
            <Moment
              style={{ margin: "5rem" }}
              key={count.value}
              moment={moments[i]}
              update={UpdateMoment}
              remove={RemoveMoment}
            />
          );
        })}
      </CardDeck>
      {*/}
    </div>
  );
}

export default App;
